* {
  margin: 0;
  border: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  font-family: "Oswald";
  font-size: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Oswald";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

:root {
  font-size: calc(0.6em + 1vw);
}

.container {
  max-width: 700px;
  padding: 0 20px;
  margin: 0 auto;
}

.accordion__item:not(:last-child) {
  margin-bottom: 20px;
}

.accordion__control {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 1rem 1.2rem;
  border: 2px solid #222;
  cursor: pointer;
  will-change: max-height;
}

.accordion__title {
  font-size: 1em;
}

.accordion__icon {
  position: relative;
  width: 1rem;
  height: 1rem;
  transition: transform 0.4s ease-in;
}

.accordion__icon::before, .accordion__icon::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 10px;
  height: 1px;
  background-color: #222;
  transform: translate(-50%, -50%);
}

.accordion__icon::after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.accordion__content {
  visibility: hidden;
  overflow: hidden;
  max-height: 0;
  opacity: 0;
  font-size: 1rem;
  padding: 0 1.2rem;
  border: 2px solid #222;
  border-top: none;
  box-sizing: content-box;
  transition: all 0.4s ease-in;
}

.open .accordion__content {
  visibility: visible;
  padding: 20px;
  opacity: 1;
}

.open .accordion__icon {
  transform: rotate(45deg);
}

.tabs {
  max-width: 400px;
  margin: 0 auto;
}

.tabs__navigation {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  max-width: 100%;
}

.tabs__tab {
  font-size: 1rem;
  color: #ccc;
  transition: color 0.4s ease-in;
  cursor: pointer;
}

.tabs__tab:not(:last-child) {
  margin-right: 20px;
}

.tabs__tab.active {
  color: #222;
}

.tabs__content {
  display: none;
}

.tabs__content.show {
  display: block;
  animation: fade 0.4s ease-in;
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
